import Vue from 'vue'
import Router from 'vue-router'
import Start from './views/start.vue'
import Round from './views/round.vue'
import Result from './views/result.vue'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'start',
            component: Start
        },
        {
            path: '/round/:hash',
            name: 'round',
            component: Round
        },
        {
            path: '/result/:hash',
            name: 'result',
            component: Result
        }
    ]
})
