<template>
    <div class="component equation-string">
        <block v-if="Number.isInteger(numberA)" class="number" v-bind:value="numberA"></block>
        <block v-if="operator" class="operator" v-bind:value="operator"></block>
        <block v-if="Number.isInteger(numberB)" class="number" v-bind:value="numberB"></block>
        <block class="equals" v-if="hasValidResult" value="="></block>
        <block class="result" v-if="hasValidResult" v-bind:value="result"></block>
        <div class="overlay" v-on:click="onClick">
        </div>
    </div>
</template>

<script>
    import Block from '@/components/block'

    export default {
        name: 'equation-string',
        data() {
            return {}
        },
        props: {
            numberA: Number,
            numberB: Number,
            operator: String,
            result: Number
        },
        computed: {
            hasValidResult() {
                return Number.isInteger(this.result);
            }
        },
        methods: {
            onClick() {
                this.$emit('clicked');
            }
        },
        components: {
            Block
        }
    }
</script>

