var mixin = {
    methods: {
        animate(element, classes) {
            return new Promise((resolve, reject) => {
                element.addEventListener("animationend", () => {
                    classes.forEach((className) => {
                        element
                            .classList.remove(className);
                    });
                    resolve();
                }, {
                    once: true
                });

                classes.forEach((className) => {
                    element
                        .classList.add(className);
                });
            });
        }
    }
};

export default mixin;