import * as types from './mutation-types'

// initial state
const state = {
    selectedIndexes: [],
    availableNumbers: [],
    operator: null
};

// getters
const getters = {
    availableNumbers(state) {
        return state.availableNumbers;
    },
    numberA(state) {
        if (state.selectedIndexes[0] === undefined) {
            return null;
        }

        return state.availableNumbers[state.selectedIndexes[0]];
    },
    numberB(state) {
        if (state.selectedIndexes[1] === undefined) {
            return null;
        }

        return state.availableNumbers[state.selectedIndexes[1]];
    },
    operator(state) {
        return state.operator;
    },
    selectedIndexes(state) {
        return state.selectedIndexes;
    },
    result(state, getters) {
        if (!Number.isInteger(getters.numberA) || !Number.isInteger(getters.numberB) || !state.operator) {
            return;
        }

        switch (state.operator) {
            case '+':
                return getters.numberA + getters.numberB;
            case '-':
                return getters.numberA - getters.numberB;
            case '/':
                return getters.numberA / getters.numberB;
            case '*':
                return getters.numberA * getters.numberB;
            default:
                return;
        }
    },
    hasValidResult(state, getters) {
        return Number.isInteger(getters.result);
    }
};

// actions
const actions = {}

// mutations
const mutations = {
    [types.ADD_OR_REMOVE_SELECTED_INDEX](state, index) {
        let selectedIndexesIndex = state.selectedIndexes.indexOf(index);

        if (selectedIndexesIndex !== -1) {
            return state.selectedIndexes.splice(selectedIndexesIndex, 1);
        }

        if (state.selectedIndexes.length === 2) {
            state.selectedIndexes.splice(0, 1);
        }

        state.selectedIndexes.push(index);
    },
    [types.NUMBER_A](state, numberA) {
        state.numberA = numberA;
    },
    [types.NUMBER_B](state, numberB) {
        state.numberB = numberB;
    },
    [types.OPERATOR](state, operator) {
        state.operator = operator;
    },
    [types.AVAILABLE_NUMBERS](state, availableNumbers) {
        state.availableNumbers = availableNumbers;
    },
    [types.RESET](state) {
        state.selectedIndexes = [];
        state.operator = null;
    }
};

export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}