import axios from 'axios'
import config from '../config'

export default {
    findOrCreateTrack(seed, length, containsMultiplyByZero, containsZero, containsDoubleOccurrence) {
        return axios.post(config.api.path + '/tracks', {
            seed: seed,
            length: length,
            contains_multiply_by_zero: containsMultiplyByZero,
            contains_zero: containsZero,
            contains_double_occurrence: containsDoubleOccurrence
        })
    },
    getTrackByHash(hash) {
        return axios.get(config.api.path + '/tracks/' + hash);
    },
    getNumbersTrackByHash(hash) {
        return axios.get(config.api.path + '/numbers/track-hash/' + hash);
    },
    findNumbers(length, containsMultiplyByZero, containsZero, containsDoubleOccurrence) {
        return axios.post(config.api.path + '/numbers', {
            length: length,
            contains_multiply_by_zero: containsMultiplyByZero,
            contains_zero: containsZero,
            contains_double_occurrence: containsDoubleOccurrence
        })
    }
}