// getters
const getters = {
    startTime(state) {
        return state.startTime;
    },
    seed(state) {
        return state.seed;
    },
    numbers(state) {
        return state.numbers;
    },
    solutions(state) {
        return state.solutions;
    },
    now(state) {
        return state.now;
    },
    previousWinCount(state) {
        return state.previousWinCount;
    },
    winCount(state) {
        return state.solutions.length;
    },
    hash(state) {
        return HashService.hashNew(state.seed, state.winCount);
    }
};

// actions
const actions = {}

// mutations
const mutations = {
    [types.RESET](state) {
        state.startTime = new Date();
        state.now = new Date();
        state.previousWinCount = null;
        state.seed = null;
        state.winCount = 0;
        state.solutions = [];
    },
    [types.ADD_SOLUTION](state, solution) {
        state.solutions.push(solution);
    },
    [types.SEED](state, seed) {
        state.seed = seed;
    },
    [types.NUMBERS](state, numbers) {
        state.numbers = numbers;
    },
    [types.START_TIME](state, startTime) {
        state.startTime = startTime;
    },
    [types.NOW](state, now) {
        state.now = now;
    },
    [types.PREVIOUS_WIN_COUNT](state, previousWinCount) {
        state.previousWinCount = previousWinCount;
    }
}

export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}