<template>
    <div class="component">
        <block class="block" v-for="character in characters" v-bind:value="character"></block>
    </div>
</template>

<script>
    import Block from '../block'

    export default {
        name: 'Number',
        data() {
            return {}
        },
        props: {
            string: {
                type: String,
                required: true
            }
        },
        watch: {},
        methods: {},
        computed: {
            characters() {
                return this.string.split('');
            }
        },
        components: {
            Block
        }
    }
</script>

