import * as types from './mutation-types'

// initial state
const state = {
    now: new Date()
}

// getters
const getters = {
    now(state) {
        return state.now;
    }
}

// actions
const actions = {}

// mutations
const mutations = {
    [types.NOW](state, now) {
        state.now = now;
    }
}

export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}