<template>
    <div class="component round-score">
        <block class="block" v-for="number in numbers" v-bind:value="number"></block>
    </div>
</template>

<script>
    import Block from '@/components/block'

    export default {
        name: 'RoundScore',
        data() {
            return {

            }
        },
        props: {
            value: undefined
        },
        watch: {

        },
        methods: {

        },
        computed: {
            numbers() {
                return this.value.toString(10).split('').map((number) => {
                    return parseInt(number);
                });
            }
        },
        components: {
            Block
        }
    }
</script>

