<template>
    <div class="component start">
        <div class="container">
            <div class="text-center">
                <div>
                    <string-blocks class="title-block" v-bind:string="'LICENSE'"></string-blocks>
                </div>
                <div>
                    <block class="sub-title-block" v-for="character in toZeroCharacters"
                           v-bind:value="character"></block>
                </div>

                <hr/>

                <template v-if="isCreating">
                    <div class="loading-container">
                        <loading-spinner class="loading-spinner" color="#ffffff"></loading-spinner>
                    </div>
                </template>
                <template v-else="">
                    <div class="create-container">
                        <button v-on:click="create('easy')" class="btn btn-lg btn-orange btn-block">
                            Easy
                        </button>

                        <hr/>

                        <button v-on:click="create('hard')" class="btn btn-lg btn-orange btn-block">
                            Hard
                        </button>

                        <hr/>

                        <button v-on:click="create('random')" class="btn btn-lg btn-orange btn-block">
                            Random
                        </button>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
    import Block from '@/components/block'
    import StringBlocks from '@/components/string-blocks'
    import Client from '@/clients/api'
    import LoadingSpinner from 'vue-spinner/src/MoonLoader'
    import _ from 'lodash'

    export default {
        name: 'result',
        data() {
            return {
                isCreating: false,
                currentTitleString: 'LICENSE'
            }
        },
        props: {},
        watch: {},
        created() {

        },
        mounted() {
        },
        methods: {
            create(level) {
                this.isCreating = true;
                let seed = Math.ceil((Math.random() * 200));
                let promise;

                switch (level) {
                    case 'easy':
                        promise = Client.findOrCreateTrack(seed, 3, true, true, true);
                        break;
                    case 'hard':
                        promise = Client.findOrCreateTrack(seed, 4, false);
                        break;
                    case 'random':
                    default:
                        promise = Client.findOrCreateTrack(seed);
                        break;
                }

                promise.then((track) => {
                    let hash = _.get(track, 'data.data.hash');
                    this.$router.push({
                        name: 'round',
                        params: {
                            hash
                        }
                    })
                }).finally(() => {
                    this.isCreating = false;
                })
            }
        },
        computed: {
            toZeroCharacters() {
                return 'TO ZERO'.split('');
            }
        },
        components: {
            Block,
            StringBlocks,
            LoadingSpinner
        },
        mixins: []
    }
</script>

