<template>
    <div class="component content" v-bind:class="classes">
        <div class="value-container current" data-current-value="">
            <div class="value">{{ currentValue }}</div>
        </div>
        <div class="value-container previous" data-previous-value="">
            <div class="value">{{ previousValue }}</div>
        </div>
        <div class="overlay" v-on:touchstart="onClick" v-on:click="onClick"></div>
    </div>
</template>

<script>
    import animateMixin from '@/mixins/animate'
    import _ from 'lodash'

    export default {
        name: 'Block',
        data() {
            return {
                currentValue: this.value,
                previousValue: null
            }
        },
        props: {
            highlighted: Boolean,
            value: undefined,
            withClickAnimation: undefined
        },
        watch: {
            value(newValue, oldValue) {
                let elementCurrent = this.$el.querySelector('[data-current-value]');
                let elementPrevious = this.$el.querySelector('[data-previous-value]');

                this.previousValue = this.currentValue;
                this.currentValue = newValue;

                Promise.all([
                    this.animate(elementPrevious, ['animated', 'slideOutUp']),
                    this.animate(elementCurrent, ['animated', 'slideInUp'])
                ]).then(() => {
                    this.previousValue = null;
                })
            }
        },
        mounted() {

        },
        methods: {
            onClick(e) {
                e.preventDefault();
                this.$emit('clicked');
            }
        },
        computed: {
            classes() {
                let classes = [];

                if (this.highlighted) {
                    classes.push('highlighted');
                }

                if (this.disabled) {
                    classes.push('disabled');
                }

                return classes;
            }
        },
        components: {},
        mixins: [
            animateMixin
        ]
    }
</script>

