        box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);

        &.highlighted {
            background: #FFF16C;
            transform: translateY(2px);
            box-shadow: 1px 1px 1em rgba(0, 0, 0, 0.5);
        }

    }

    .overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }

    .value-container {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        animation-duration: .5s;

        .value {
            position: absolute;
            top: 50%;
            right: 0;
            bottom: 0;
            left: 0;
            font-size: 6em;
