<template>
    <div class="component">
        <div class="row">
            <div class="col-6 text-left">
                <span class="challenge-starting-numbers">{{ solution.challengeStartingNumbers.join('') }}</span>
                <span class="time">{{ timeParts }} seconds</span>
            </div>

            <div class="col-6 text-right">
                <div class="commit" v-for="commit in solution.commits">
                    <equation-string
                            class="equation-string"
                            v-bind:number-a="commit.numberA"
                            v-bind:number-b="commit.numberB"
                            v-bind:operator="commit.operator"
                            v-bind:result="commit.result"></equation-string>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import moment from 'moment'
    import Block from '@/components/block'
    import EquationString from '@/components/equation-string'

    export default {
        name: 'ResultSolution',
        data() {
            return {}
        },
        props: {
            solution: undefined
        },
        watch: {},
        methods: {},
        computed: {
            timeParts() {
                return moment.utc(this.solution.time).format('s.SSS');
            }

        },
        components: {
            Block,
            EquationString
        }
    }
</script>

