export default {
    hashParse(hash) {
        try {
            let data = atob(hash);
            return JSON.parse(data);
        } catch (e) {
            console.log(e);
            return null;
        }
    },
    hashNew(seed = null, previousWinCount = null) {

        if (!seed) {
            // Total highways - 830
            seed = Math.floor((Math.random() * 830));
        }

        return btoa(JSON.stringify({
            seed: seed,
            previousWinCount: previousWinCount
        }));
    }
}