<template>
    <div class="component page">
        <div class="text-center">
            <div class="top">
                <div class="container">
                    <div class="row">
                        <div class="col-6 text-left">
                            <router-link v-bind:to="{'name': 'round', params:{hash:hash}}"
                                         class="btn btn-sm btn-block btn-orange">
                                Try again
                            </router-link>
                            <router-link v-bind:to="{'name': 'start'}"
                                         class="btn btn-sm btn-block btn-orange">
                                New round
                            </router-link>
                            <span v-on:click="share" class="btn btn-sm btn-block btn-orange">Challenge</span>
                        </div>
                        <div class="col-6 text-right">
                            <block v-bind:value="roundWinCount" class="round-win-count"></block>
                        </div>
                    </div>
                </div>
            </div>

            <div class="statistics">
                <template v-if="hasSolutions">

                    <div class="container">
                        <div class="row">
                            <div class="col-12 text-center">
                                <span class="title">Average time per challenge</span>
                                <span class="average-time-per-equation">{{ averageTimePerEquation.toFixed(3) }} seconds</span>
                            </div>
                        </div>
                    </div>

                    <hr/>

                    <div class="container">
                        <div class="row">
                            <div class="col-12 text-left">
                                <span class="title">Solutions</span>
                            </div>
                        </div>
                        <template v-for="solution in roundSolutions">
                            <result-solution class="solution"
                                             v-bind:solution="solution"></result-solution>
                            <hr/>
                        </template>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import HashService from '@/services/hash'
    import Block from '@/components/block'
    import ResultSolution from '@/components/result/solution'

    export default {
        name: 'result',
        data() {
            return {}
        },
        props: {},
        watch: {},
        created() {

        },
        mounted() {
        },
        methods: {
            share() {
                if (navigator.share) {
                    navigator.share({
                        title: 'Challenge',
                        text: 'New challenge',
                        url: this.challengeUrl,
                    })
                        .then(() => console.log('Successful share'))
                        .catch((error) => console.log('Error sharing', error));
                } else {
                    window.location.href = this.challengeUrl;
                }
            }
        },
        computed: {
            hasSolutions() {
                return this.roundSolutions.length > 0;
            },
            averageTimePerEquation() {
                let totalTime = 0;
                this.roundSolutions.forEach((solution) => {
                    totalTime += solution.time;
                });

                return ((totalTime / this.roundSolutions.length) / 1000);
            },
            averageTimePerEquationArray() {
                return this.averageTimePerEquation.toFixed(3).toString().split('');
            },
            hash() {
                return this.$route.params['hash'];
            },
            challengeUrl() {
                return window.location.protocol + '//' + window.location.host + '/#/round/' + this.hash;
            },
            ...mapGetters('round', {
                'roundWinCount': 'winCount',
                'roundHash': 'hash',
                'roundSeed': 'seed',
                'roundSolutions': 'solutions'
            })
        },
        components: {
            Block,
            ResultSolution
        },
        mixins: []
    }
</script>

