import {Howl} from 'howler'

export default {
    sounds: {
        challengeCompleted: new Howl({src: ['/sounds/challenge-completed.wav']}),
        click: new Howl({src: ['/sounds/click.mp3']}),
        heartbeat: new Howl({src: ['/sounds/heartbeat.mp3']}),
        clock: new Howl({src: ['/sounds/clock.wav']}),
        woosh: new Howl({src: ['/sounds/woosh.wav']}),
        commit: new Howl({src: ['/sounds/commit.wav']}),
        beepLow: new Howl({src: ['/sounds/beep-low.wav']}),
        beepHigh: new Howl({src: ['/sounds/beep-high.wav']})
    },
    playHeartbeat() {
        this.sounds.heartbeat.play();
    },
    playClick() {
        this.sounds.click.play();
    },
    playClock() {
        this.sounds.clock.play();
    },
    playCommit() {
        this.sounds.commit.play();
    },
    playWoosh() {
        this.sounds.woosh.play();
    },
    playChallengeCompleted() {
        this.sounds.challengeCompleted.play();
    },
    playBeepLow() {
        this.sounds.beepLow.play();
    },
    playBeepHigh() {
        this.sounds.beepHigh.play();
    }
}