<template>
    <div class="component">
        <div class="text-center">
            <interstate class="interstate" style="font-size: 30px;" v-bind:number="roundSeed"></interstate>
            <br/>
            <block style="font-size: 8px;" v-bind:value="countdownNumber"></block>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import Interstate from '@/components/interstate'
    import Block from '@/components/block'
    import AnimateMixin from '@/mixins/animate'
    import SoundService from '@/services/sound'

    export default {
        name: 'RoundIntro',
        data() {
            return {
                countdownNumber: this.number,
                countdownTimeout: null
            }
        },
        props: {
            number: {
                default: 4
            }
        },
        watch: {},
        methods: {
            onCountdown() {
                this.countdownNumber--;

                if (this.countdownNumber > 0) {
                    SoundService.playBeepLow();
                    this.countdownTimeout = setTimeout(() => {
                        this.onCountdown();
                    }, 1000);
                } else {
                    SoundService.playBeepHigh();
                    this.animate(this.$el, ['animated', 'slideOutRight']).then(() => {
                        this.$emit('ended');
                    })
                }
            }
        },
        mounted() {
            this.animate(this.$el, ['animated', 'slideInLeft']).then(() => {
                this.onCountdown();
            });
        },
        beforeDestroy() {
            clearTimeout(this.countdownTimeout);
        },
        mixins: [AnimateMixin],
        computed: {
            ...mapGetters('round', {
                'roundSeed': 'seed'
            })
        },
        components: {
            Interstate,
            Block
        }
    }
</script>

