<template>
    <div class="component page">
        <template v-if="isInitialized">
            <div class="controls-container">
                <div class="text-center">
                    <div class="row">
                        <div class="col-6 text-left">
                                <span class="control-button btn-orange" v-on:click="navigateBack()">
                                    <i class="material-icons">arrow_left</i>
                                </span>
                        </div>
                        <div class="col-6 text-right">
                            <span class="control-button btn-orange" v-on:click="refresh()">
                                    <i class="material-icons">refresh</i>
                                </span>
                        </div>
                    </div>
                </div>
            </div>

            <div class="statistics-container animated slideInDown">
                <template v-if="started">
                    <div class="row">
                        <div class="col-4 text-left">
                            <round-score v-bind:value="roundWinCount"></round-score>
                        </div>
                        <div class="col-8 text-right">
                            <round-time v-bind:value="timeRemainingDisplay"></round-time>
                        </div>
                    </div>
                </template>
            </div>

            <div class="plates-container" v-show="started">
                <div class="plate-container">
                    <plate v-bind:numbers="challengeStartingNumbers"
                           v-bind:plate-length="7"
                           data-plate-current=""
                           class="plate"
                    ></plate>
                </div>

                <div class="plate-container">
                    <plate v-show="showPreviousNumber"
                           v-bind:numbers="previousNumber"
                           v-bind:plate-length="7"
                           data-plate-previous=""
                           class="plate"
                    ></plate>
                </div>
            </div>

            <div class="equation-strings-container text-center">
                <transition-group
                        name="equation-string-transition-group"
                        enter-active-class="animated slideInLeft"
                        v-bind:duration="0"
                >
                    <equation-string v-for="(commit, index) in challengeCommits"
                                     v-bind:key="'equation-string-' + index"
                                     v-bind:number-a="commit.numberA"
                                     v-bind:number-b="commit.numberB"
                                     v-bind:operator="commit.operator"
                                     v-bind:result="commit.result"
                                     v-bind:locked="true"
                                     v-on:clicked="() => {removeCommit(index)}"
                                     class="equation-string"
                                     style="font-size: 3px;"
                    ></equation-string>
                </transition-group>
            </div>


            <div class="input-container animated slideInUp text-center">
                <div class="input-equation-string">
                    <template v-if="started">
                        <equation-string v-bind:number-a="equationInputNumberA"
                                         v-bind:number-b="equationInputNumberB"
                                         v-bind:operator="equationInputOperator"
                                         v-bind:result="equationInputResult"
                                         style="font-size:3px; color: white;"
                        ></equation-string>
                    </template>
                </div>

                <div class="input-equation-input">
                    <template v-if="started">
                        <equation-input v-bind:available-numbers="challengeRemainingNumbers"
                                        v-bind:length="challengePoolNumbers.length"
                                        style="font-size: 6px"
                                        v-on:commit="onCommit"></equation-input>
                    </template>
                </div>
            </div>

            <template v-if="!started">
                <div class="intro-container">
                    <intro v-bind:number="3" v-on:ended="onIntroEnded"></intro>
                </div>
            </template>
        </template>
        <template v-else="">
            <div class="loading-container">
                <string-blocks style="margin-bottom: 2em;" v-bind:string="'LOADING'"></string-blocks>
                <loading-spinner style="display: inline-block" color="#ffffff"></loading-spinner>
            </div>
        </template>

        <div class="debug" v-if="debug">
            <ul>
                <li>roundSolutions: {{ roundSolutions }}</li>
                <li>challengeStartingNumbers {{ challengeStartingNumbers }}</li>
                <li>previousNumber {{ previousNumber }}</li>
            </ul>
        </div>
    </div>
</template>

<script>
    import * as TimeMutationTypes from '@/store/modules/time/mutation-types'
    import * as RoundMutationTypes from '@/store/modules/round/mutation-types'
    import * as EquationInputMutationTypes from '@/store/modules/equation-input/mutation-types'
    import * as ChallengeMutationTypes from '@/store/modules/challenge/mutation-types'

    import Client from '../clients/api'
    import LoadingSpinner from 'vue-spinner/src/MoonLoader'
    import {mapGetters} from 'vuex'
    import SoundService from '@/services/sound'
    import RandomService from '@/services/random'
    import Plate from '@/components/plate'
    import RoundScore from '@/components/round/score'
    import RoundTime from '@/components/round/time'
    import Block from '@/components/block'
    import StringBlocks from '@/components/string-blocks'
    import Intro from '@/components/round/intro'
    import EquationInput from '@/components/equation-input'
    import EquationString from '@/components/equation-string'
    import animateMixin from '@/mixins/animate'


    export default {
        name: 'Input',
        data() {
            return {
                started: false,
                isInitialized: false,
                commits: [],
                previousNumber: [],
                roundTimeLength: (30 * 1000),
                roundEnded: false,
                showPreviousNumber: false,
                debug: false,
                lastCompletedDate: new Date()
            }
        },
        props: {
            msg: String
        },
        watch: {
            timeRemainingDisplay(newValue) {
                if (this.started) {
                    SoundService.playClock();
                }

                if (newValue < 6) {
                    SoundService.playHeartbeat();
                }
            },
            timeRemaining(newValue) {
                if (newValue < 0) {
                    this.onRoundEnded();
                }
            },
            challengeRemainingNumbers(remainingNumbers) {
                this.$store.commit('equationInput/' + EquationInputMutationTypes.RESET, remainingNumbers);
                this.$store.commit('equationInput/' + EquationInputMutationTypes.AVAILABLE_NUMBERS, remainingNumbers);
            },
            challengeCompleted(newBool) {
                if (newBool === true) {
                    this.onCompleted();
                }
            }
        },
        created() {

        },
        mounted() {
            let hash = this.$route.params['hash'];

            this.initialize(hash)
                .then(() => {
                    this.isInitialized = true;
                });
        },
        methods: {
            navigateBack() {
                this.$router.push({
                    name: 'start'
                });
            },
            refresh() {
                this.started = false;
                this.initialize(this.$route.params['hash']);
            },
            initialize(hash) {
                this.$store.commit('round/' + RoundMutationTypes.RESET);

                return Client.getTrackByHash(hash)
                    .then((result) => {
                        let track = result.data.data;
                        this.$store.commit('round/' + RoundMutationTypes.SEED, track.seed);
                        RandomService.setSeed(this.roundSeed);
                        return Client.getNumbersTrackByHash(hash);
                    })
                    .then((result) => {
                        let numbers = result.data.data;
                        this.$store.commit('round/' + RoundMutationTypes.NUMBERS, numbers);
                        this.$store.commit('challenge/' + ChallengeMutationTypes.STARTING_NUMBERS, this.getNextNumber());
                    });
            },
            onRoundEnded() {
                console.log(JSON.stringify(this.roundSolutions));

                this.$router.push({
                    'name': 'result',
                    params: {
                        hash: this.$route.params['hash']
                    }
                });
            },
            onIntroEnded() {
                this.start();
            },
            start() {
                this.lastCompletedDate = new Date();
                this.onTick();
                this.$store.commit('round/' + RoundMutationTypes.START_TIME, new Date());
                this.started = true;
            },
            onTick() {
                this.$store.commit('time/' + TimeMutationTypes.NOW, new Date());
                if (!this.roundEnded) {
                    window.requestAnimationFrame(this.onTick);
                }
            },
            onCompleted() {
                SoundService.playChallengeCompleted();

                let solution = {
                    commits: this.challengeCommits,
                    time: new Date().getTime() - this.lastCompletedDate.getTime(),
                    challengeStartingNumbers: this.challengeStartingNumbers
                };

                this.lastCompletedDate = new Date();
                this.$store.commit('round/' + RoundMutationTypes.ADD_SOLUTION, solution);
                this.$emit('complete');
                this.setNextOrigin();
            },
            onCommit(value) {
                this.$store.commit('challenge/' + ChallengeMutationTypes.ADD_COMMIT, value);
            },
            removeCommit(index) {
                SoundService.playWoosh();
                this.$store.commit('challenge/' + ChallengeMutationTypes.REMOVE_COMMIT_AT_INDEX, index);
            },
            getNextNumber() {
                let index = Math.floor(RandomService.generate() * this.roundNumbers.length);
                return this.roundNumbers[index].toString().split('').map((number) => {
                    return parseInt(number);
                });
            },
            setNextOrigin() {
                this.commits = [];

                this.previousNumber = this.challengeStartingNumbers;
                this.$store.commit('challenge/' + ChallengeMutationTypes.STARTING_NUMBERS, this.getNextNumber());

                let currentElement = this.$el.querySelector('[data-plate-current]');
                let previousElement = this.$el.querySelector('[data-plate-previous]');

                if (!currentElement || !previousElement) {
                    return;
                }

                this.showPreviousNumber = true;

                Promise.all([
                        this.animate(currentElement, ['animated', 'bounceInLeft']),
                        this.animate(previousElement, ['animated', 'bounceOutRight'])
                    ]
                ).then(() => {
                    this.showPreviousNumber = false;
                });
            }
        },
        computed: {
            roundTime() {
                return this.now.getTime() - this.roundStartTime.getTime();
            },
            roundTimeDisplay() {
                return Math.ceil(this.roundTime / 1000);
            },
            timeRemaining() {
                return this.roundTimeLength - this.roundTime;
            },
            timeRemainingDisplay() {
                return Math.ceil(this.timeRemaining / 1000);
            },
            challengeCommitsMappedAndReversed() {
                return this.challengeCommits.map((commit, index) => {
                    return {
                        commit: commit,
                        index: index
                    }
                }).reverse();
            },
            ...mapGetters('time', {
                now: 'now'
            }),
            ...mapGetters('round', {
                roundRandomFloat: 'randomFloat',
                roundSeed: 'seed',
                roundNumbers: 'numbers',
                roundWinCount: 'winCount',
                roundPreviousWinCount: 'previousWinCount',
                roundStartTime: 'startTime',
                roundSolutions: 'solutions'
            }),
            ...mapGetters('challenge', {
                challengeCompleted: 'completed',
                challengeStartingNumbers: 'startingNumbers',
                challengeUsedNumbers: 'usedNumbers',
                challengeRemainingNumbers: 'remainingNumbers',
                challengePoolNumbers: 'poolNumbers',
                challengeCommits: 'commits'
            }),
            ...mapGetters('equationInput', {
                equationInputNumberA: 'numberA',
                equationInputNumberB: 'numberB',
                equationInputOperator: 'operator',
                equationInputResult: 'result'
            })
        },
        components: {
            Intro,
            EquationString,
            EquationInput,
            Block,
            StringBlocks,
            Plate,
            RoundScore,
            RoundTime,
            LoadingSpinner
        },
        mixins: [
            animateMixin
        ]
    }
</script>

