import * as types from './mutation-types'
import config from "@/config";

// initial state
const state = {
    startingNumbers: [],
    commits: []
};

// getters
const getters = {
    commits(state) {
        return state.commits;
    },
    startingNumbers(state) {
        return state.startingNumbers;
    },
    poolNumbers(state, getters) {
        let numbers = getters.startingNumbers.slice();

        Object.keys(getters.commits).forEach((key) => {
            let number = getters.commits[key].result;
            numbers.push(number);
        });

        return numbers;
    },
    usedNumbers(state, getters) {
        let numbers = [];
        Object.keys(getters.commits).forEach((key) => {
            numbers.push(getters.commits[key].numberA);
            numbers.push(getters.commits[key].numberB);
        });

        return numbers;
    },
    remainingNumbers(state, getters) {
        let numbers = getters.poolNumbers.slice();

        getters.usedNumbers.forEach((usedNumber) => {
            numbers.splice(numbers.indexOf(usedNumber), 1);
        });

        return numbers;
    },
    completed(state, getters) {
        if (getters.remainingNumbers.length === 0) {
            return false;
        }

        if (getters.remainingNumbers.length === 1) {
            return getters.remainingNumbers[0] === 0;
        }

        let onlyZeros = true;

        getters.remainingNumbers.forEach((number) => {
            if (number !== 0) {
                onlyZeros = false;
            }
        });

        if (onlyZeros) {
            return true;
        }

        // Two and equal
        if (getters.remainingNumbers.length === 2 && getters.remainingNumbers[0] === getters.remainingNumbers[1]) {
            return true;
        }

        // Two and a zero
        if (getters.remainingNumbers.length === 2 && (getters.remainingNumbers[0] === 0 || getters.remainingNumbers[1] === 0)) {
            return true;
        }

        return false;
    },
    randomMultiplier(state) {
        return (config.possibleNumbers.indexOf(parseInt(state.startingNumbers.join(''))) / config.possibleNumbers.length);
    }
}

// actions
const actions = {};

// mutations
const mutations = {
    [types.STARTING_NUMBERS](state, startingNumbers) {
        state.commits = [];
        state.startingNumbers = startingNumbers;
    },
    [types.ADD_COMMIT](state, commit) {
        state.commits.push(commit);
    },
    [types.REMOVE_COMMIT_AT_INDEX](state, index) {
        state.commits = state.commits.splice(0, index);
    }
};

export default {
    state,
    getters,
    actions,
    mutations,
    namespaced: true
}