<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>
    import store from 'store'
    import uuid from 'uuid/v1'
    import LocalStorageKeys from '@/constants/LocalStorageKeys'

    export default {
        name: 'app',
        data() {
            return {}
        },
        props: {},
        watch: {},
        created() {
            let userUUID = store.get(LocalStorageKeys.USER_UUID);
            if (!userUUID) {
                store.set(LocalStorageKeys.USER_UUID, uuid());
            }

            console.log(store.get(LocalStorageKeys.USER_UUID));
        },
        mounted() {
        },
        methods: {},
        computed: {},
        components: {},
        mixins: []
    }
</script>

