<template>
    <div class="component plate">
        <div class="character-container" v-bind:style="characterContainerStyle">
            <span class="title" v-bind:style="titleStyle">{{ stateName }}</span>
            <span class="characters" v-bind:style="charactersStyle">{{ plateString }}</span>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import animateMixin from '@/mixins/animate'
    import config from '@/config.js'
    import Alea from 'alea'

    export default {
        name: 'Number',
        data() {
            return {
                plateString: null
            }
        },
        props: {
            plateLength: Number,
            numbers: Array
        },
        watch: {
            numbers(newValue) {
                this.plateString = this.getPlateString();
            }
        },
        methods: {
            onComplete() {
                this.animate(this.$el, [
                    'animated',
                    'tada'
                ])
            },
            getPlateString() {
                this.randomService = new Alea(this.numbers.join(''));

                let strings = [];
                let remainder = this.plateLength - this.numbers.length;

                let letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
                while (remainder--) {
                    strings.push(letters.charAt(Math.floor(this.randomService() * letters.length)));
                }

                strings = strings.concat(this.numbers);

                return this.shuffle(strings).join('');
            },
            shuffle(array) {
                let currentIndex = array.length;
                let temporaryValue, randomIndex;

                while (0 !== currentIndex) {
                    randomIndex = Math.floor(this.randomService() * currentIndex);
                    currentIndex -= 1;
                    temporaryValue = array[currentIndex];
                    array[currentIndex] = array[randomIndex];
                    array[randomIndex] = temporaryValue;
                }

                return array;

            }
        },
        mounted() {
            this.plateString = this.getPlateString();
        },
        created() {
            this.$parent.$on('complete', this.onComplete);
        },
        computed: {
            numbersJoined() {
                return parseInt(this.numbers.join(''));
            },
            stateSettings() {
                let index = Math.floor((this.roundNumbers.indexOf(this.numbersJoined) / this.roundNumbers.length) * config.licensePlates.length);

                if (index < 0) {
                    index = 0;
                }

                console.log(index);

                return config.licensePlates[index];
            },
            titleStyle() {
                return {
                    'color': this.stateSettings['textColor']
                }
            },
            charactersStyle() {
                return {
                    'color': this.stateSettings['textColor']
                }
            },
            stateName() {
                return this.stateSettings['state'];
            },
            characterContainerStyle() {
                return {
                    'background': this.stateSettings['backgroundColor']
                }
            },
            ...mapGetters('round', {
                'roundNumbers': 'numbers'
            }),
            ...mapGetters('challenge', {
                'challengeRandomMultiplier': 'randomMultiplier'
            })
        },
        components: {},
        mixins: [
            animateMixin
        ]
    }
</script>

