<template>
    <div class="component">
        <div class="container">
            <div class="row">
                <div class="col-12">
                    <block
                            v-for="(number, index) in availableNumbers"
                            v-bind:key="'block-number' + index"
                            v-bind:value="number"
                            v-bind:highlighted="selectedIndexes.indexOf(index) !== -1"
                            v-on:clicked="() => { onNumberClicked(index, number) }"
                            class="number"
                    >
                    </block>

                    <br/>

                    <block
                            v-for="(operatorOption, index) in operators"
                            v-bind:key="'block-operator' + index"
                            v-bind:value="operatorOption"
                            class="operator"
                            v-bind:highlighted="operator === operatorOption"
                            v-on:clicked="() => { setOperator(operatorOption) }">
                    </block>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import SoundService from '@/services/sound'
    import * as EquationInputMutationTypes from '@/store/modules/equation-input/mutation-types'
    import {mapGetters} from 'vuex'
    import EquationString from '@/components/equation-string'
    import Block from '@/components/block'

    export default {
        name: 'Input',
        data() {
            return {
                operators: [
                    '+',
                    '-',
                    '*',
                    '/'
                ]
            }
        },
        props: {
            value: Number
        },
        watch: {
            hasValidResult(newBoolean) {
                if (newBoolean === true) {
                    this.commit();
                }
            }
        },
        methods: {
            setOperator(operator) {
                SoundService.playClick();
                if (this.operator === operator) {
                    this.$store.commit('equationInput/' + EquationInputMutationTypes.OPERATOR, null);
                } else {
                    this.$store.commit('equationInput/' + EquationInputMutationTypes.OPERATOR, operator);
                }
            },
            onNumberClicked(index, number) {
                SoundService.playClick();
                this.$store.commit('equationInput/' + EquationInputMutationTypes.ADD_OR_REMOVE_SELECTED_INDEX, index);
            },
            commit() {
                if (!Number.isInteger(this.result)) {
                    return;
                }

                SoundService.playCommit();
                this.$emit('commit', {
                    numberA: this.numberA,
                    numberB: this.numberB,
                    operator: this.operator,
                    result: this.result
                });

                this.reset();
            },
            reset() {

            }
        },
        computed: {
            ...mapGetters('equationInput', [
                'availableNumbers',
                'hasValidResult',
                'operator',
                'selectedIndexes',
                'numberA',
                'numberB',
                'result'
            ])
        },
        components: {
            EquationString,
            Block
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .number,
    .operator {
        margin: 1em;
    }
</style>
